.dashboard-layout {
  font-family: Aeroport, serif;
  display: grid;
  grid-template-columns: 4fr 8fr;
  height: 95%;
  margin-top: 15px;
  margin-bottom: 15px;
  color: #303643;
  .explorer {
    margin-left: 10px;
    background-color: #FFF;
    border-radius: 16px;
    margin-right: 5px;
    padding: 10px;
    .canvas-explorer {
      border-radius: 16px;
      background-color: #EBEBEB;
      height: 40vh;
      overflow: hidden;
    }
  }
  .project {
    border-radius: 16px;
    background-color: #FFF;
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    padding: 10px;
    overflow-y: auto;

    .map-container {
      width: calc(100% - 20px);
      margin: 0 10px 0 10px;
      border-radius: 16px;
      overflow-y: hidden;
      height: 50vh;

      .map {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: calc(100% - 20px);
        height: calc(100% - 20px);
        border-radius: 16px;
        .ol-viewport {
          border-radius: 16px;
        }
      }
      .project-pointer {
        position: relative;
        top: 0;
        left: -50%;
        transform: translateY(-100%);
        width: 40px;
        height: 40px;
        z-index: 99;
      }
      .ol-popup {
        position: relative;
        background-color: white;
        box-shadow: 0 1px 4px rgba(0,0,0,0.2);
        padding: 15px;
        border-radius: 10px;
        border: 1px solid #cccccc;
        top: -90%;
        left: 6%;
        width: 25%;
        height: 120px;
        min-width: 240px;
      }
      .ol-tool-tip {
        background-color: rgba(255,255,255,0.5);
        box-shadow: 0 1px 4px rgba(0,0,0,0.2);
        padding: 5px;
        border-radius: 10px;
        border: 1px solid #cccccc;
        text-align: center;
        h5 {
          margin: 2px;
          padding: 0;
          text-transform: capitalize;
        }
        p {
          margin: 0;
          padding: 0;
        }
      }
    }
  }
}

.pop-in_background {
  background-color: #EBEBEB;
  opacity: 0.5;
  position: absolute;
  width: calc(100% - 70px);
  height: calc(100% - 6.2em);
  z-index: 98;
}
.pop-in {
  background-color: #232937;
  color: #EBEBEB;
  opacity: revert;
  width: 60%;
  height: 65%;
  min-height: 350px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -40%);
  z-index: 99;
  border-radius: 16px;
  display: grid;
  grid-template-columns: 10fr 2fr;
  .pop-in_goback {
    margin-top: 5px;
    margin-right: 5px;
    float: right;
    background-color: #303643;
    color: white;
    border-radius: 48px;
    height: 50px;
    width: 50px;
    svg {
      position: relative;
      top: 42%;
      left: 48%;
      transform: translate(-50%, -50%);
    }
  }
  .pop-in_container {
    width: 80%;
    height: 100%;
    position: relative;
    left: 60%;
    padding-top: 20px;
    padding-bottom: 20px;
    transform: translateX(-50%);
    display: grid;
    grid-template-rows: 0.5fr 0.5fr 3fr;
    grid-row-gap: 5px;
    p {
      margin-bottom: 5px;
    }
    .title {
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      width: fit-content;
      font-weight: bold;
      font-size: 2em;
    }
    .subtitle {
      font-size: 0.9em;
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      width: 80%;
      text-align: center;
    }
    .content {
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      min-width: 350px;
      width: 100%;
      margin-top: 20px;
      .mail-container {
        padding-top: 25px;
        overflow-y: auto;
        max-height: 180px;
        height: 50%;
        .email-line {
          display: inline-flex;
          grid-template-columns: 200px 20%  20% 20%;
          margin-bottom: 5px;
          .radio-btn {
            display: inline-flex;
            span {
              padding: 5px;
              font-size: 0.7em;
              position: relative;
            }
          }
          .email {
            span {
              -webkit-transition: 2s;
              -moz-transition: 2s;
              transition: 2s;

              -webkit-transition-timing-function: linear;
              -moz-transition-timing-function: linear;
              transition-timing-function: linear;
              position: absolute;
              transform: translateX(0);
            }
            color: #1e2432;
            padding: 3px 15px 3px 15px;
            background-color: white;
            border-radius: 8px;
            max-width: 150px;
            width: 150px;
            margin-right: 10px;
            white-space: nowrap;
            overflow: hidden;
            position: relative;
          }
          .email:hover {
            background-color: #d34b4b;
          }
          .email:hover span {
            transform: translateX(calc( 120px - 100%));
          }
        }
      }
      .input-container {
        background-color: white;
        border-radius: 48px;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
        height: 48px;

        input {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          background: transparent;
          padding: 12px 12px 12px 24px;
          border-radius: 48px;
          border-style: none;
          outline: none;
          font-size: 16px;
          color: #333;
          font-weight: 300;
        }

        span {
          position: absolute;
          top: 1px;
          left: 15px;
          padding: 13px;
          display: inline-block;
          font-size: 16px;
          color: #959595;
          font-weight: 300;
          transition: 0.5s;
          pointer-events: none;
        }

        .icon {
          color: white;
          font-weight: 500;
          width: 30px;
          height: 30px;
          position: relative;
          top: 50%;
          left: 93%;
          margin-right: 20px;
          transform: translate(-50%, -50%);
        }
        .light {
          color: #1E2432;
        }
      }
      .sign_input-container {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 15px;
        bottom: 10px;
      }
    }
  }
}

.errorMsg-popin {
  color: #dc2626;
  font-size: 0.8em;
  margin-left: 20px;
  width: initial;
  position: relative;
  bottom: -50px;
}

.input-container input:focus ~ span,
.input-container input:hover ~ span,
.input-container input:valid ~ span {
  transform: translateX(-10px) translateY(-40px);
  font-size: 12px;
}
.input-container input[type="button"],
.input-container input.submit.light {
  color: #1E2432;
  border-radius: 48px;
  background-color: white;
  cursor: pointer;
  font-weight: 500;
  text-align: left;
}